import React, { useState, useRef, useEffect } from 'react'
import { Configuration, OpenAIApi } from 'openai'
import { Autocomplete, Box, Checkbox, FormControlLabel, TextField, FormHelperText } from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import Container from '@mui/material/Container';
// import { itinerary } from '../data/itinerary';
import './Itinerary.css'
import ActivityDetail from '../../components/Activity/ActivityDetail';
import DiningDetails from '../../components/Dining/DiningDetails';
import { countriesStates } from '../../data/countryStates'
import { showToast } from '../../components/Utils/Toast';
import { toast } from 'react-hot-toast';

const Itinerary = () => {
  const btnRef = useRef()

  const millisecInOneDay = 1000 * 3600 * 24;

  // dates....

  const InitialData = {
    startDate: new Date(),
    endDate: new Date((new Date()).getTime() + millisecInOneDay),
    country: "",
    state: "",
    states: [],
    place: "",
    isChecked: false,
  }

  const [formData, setFormData] = useState(InitialData)

  const [loading, setLoading] = useState(false)
  const [btnDisabled, setBtnDisabled] = useState(false);

  const [places, setPlaces] = useState();
  const [itinerary, setItinerary] = useState();

  const [error, setError] = useState(false)

  const [customPlaceName, setCustomPlaceName] = useState({
    place: "",
    error: false
  })

  const diffDays = (date1, date2) => {
    const diffTime = Math.abs(date2 - date1);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
  }

  const dayDiff = diffDays(formData.startDate, formData.endDate);

  const days = dayDiff + 1

  const getTrimedDate = (value) => {
    return String(dayjs(value)['$d']).slice(0, 15);
  }

  function generatePrompt() {
    // console.log(days);
    // console.log(getTrimedDate(startDate))

    const { place, startDate, endDate, country, state } = formData
    const { place: cusPlace } = customPlaceName

    return `
            User wants to travel to location ${place || cusPlace}
            Of Country: ${country} ${state?.name ? `State: ${state.name}` : ""}
            if location is unknown to you you can generate itinerary by taken country or state as the location 
            Start date: ${getTrimedDate(startDate)} 
            End date:${getTrimedDate(endDate)}
            The itinerary will be ${days} days long.
            Give itinerary for each day.
            Suggest places to visit.
            Include the method of transportation between places.
            Format the entire response in JSON string - important.
            keep the description of the place medium.
            Include coordinates of the places.
            also provide the departure activity inside the last day of activities.
            Make one array that includes all itinerary days in the response json 
            Example response array for day1 itinerary:

            [{
                "date": "2023-06-29",
                "day": "Day 1",
                "activities": [
                    {
                        "morning": {
                            "activity": "Morning Sightseeing",
                            "description": "Start your day by exploring the scenic beauty of Ulhasnagar. Visit the picturesque Ulhas River and enjoy a leisurely walk along its banks. Experience the tranquility of nature and capture some beautiful photographs.",
                            "transportation":"taxi",
                            "coordinates": {
                                "latitude": 52.358468,
                                "longitude": 4.881119
                            }
                        }
                    },
                    {
                        "afternoon": {
                            "activity": "Shopping at Ulhasnagar Market",
                            "description": "Start your day by exploring the scenic beauty of Ulhasnagar. Visit the picturesque Ulhas River and enjoy a leisurely walk along its banks. Experience the tranquility of nature and capture some beautiful photographs.",
                            "transportation":"walk",
                            "coordinates": {
                                "latitude": 89.358468,
                                "longitude": 3.881119
                            }
                        }
                    },
                    {
                        "evening": {
                            "activity": "Shopping at Ulhasnagar Market",
                            "description": "Start your day by exploring the scenic beauty of Ulhasnagar. Visit the picturesque Ulhas River and enjoy a leisurely walk along its banks. Experience the tranquility of nature and capture some beautiful photographs.",
                            "transportation":"rope way",
                            "coordinates": {
                                "latitude": 92.000468,
                                "longitude": 12.881119
                            }
                        }
                    },
                    {
                        "night": {
                            "activity": "Shopping at Ulhasnagar Market",
                            "description": "Start your day by exploring the scenic beauty of Ulhasnagar. Visit the picturesque Ulhas River and enjoy a leisurely walk along its banks. Experience the tranquility of nature and capture some beautiful photographs.",
                            "transportation":"bus",
                            "coordinates": {
                                "latitude": 52.358468,
                                "longitude": 4.881119
                            }
                        }
                    }
                ],
                "dine_options": [
                    {
                        "place": "Ai mahara: Seafood restaurant with an aquarium dining room",
                        "address": "Near dubai palace root no 16 vashim"
                    },
                    {
                        "place": "Cavali Club: Italian restaurant with a glamorous atmosphere",
                        "address": "Near dubai airport kolivada root 19"
                    }
                ]
            }]`
  }

  // const configuration = new Configuration({
  //   apiKey: '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',
  // });

  const openai = new OpenAIApi();

  const getItineraryData_By_GPT_Turbo = async (generatedPrompt) => {
    setLoading(true)

    const exampleMessage = [
      {
        role: 'system',
        content: 'Act as a travel itinerary detail generator',
      },
      {
        role: 'user',
        content: generatedPrompt,
      }
    ]

    try {

      const completion = await openai.createChatCompletion({
        model: 'gpt-3.5-turbo',
        messages: exampleMessage,
      });

      try {

        const { data, error } = completion
        if (error) {
          setLoading(false)
          setBtnDisabled(false)
          showToast("error", "Internal server error, Please try again", 3000)
          btnRef.current.innerText = "Generate"
          return
        }

        let parsedRes = JSON.parse(data.choices[0].message?.content)
        console.log(parsedRes);
        if (Array.isArray(parsedRes)) {
          setItinerary({
            data: parsedRes,
            place: formData.place
          })
        }
        else {
          const { itinerary } = parsedRes
          if (itinerary) {
            console.log("In else:", parsedRes);
            setItinerary({
              data: itinerary,
              place: formData.place
            })
          }
        }
        showToast("success", "Your Itinerary is ready!", 3000)
        btnRef.current.innerText = "Generate Itinerary"
      } catch (e) {
        throw new Error('The json strucure generated from gpt is not a valid one, please try again');
      }
    } catch (error) {
      setLoading(false)
      console.log("Some Error Occured", error)
    }


    setLoading(false)
    setBtnDisabled(false)
  }

  const getItineraryData_By_Text_Davinchi = async (generatedPrompt) => {
    try {
      setLoading(true)
      const result = await openai.createCompletion({
        model: "text-davinci-003",
        prompt: generatedPrompt,
        temperature: 0.2,
        max_tokens: 2024,
      });

      try {
        let parsedRes = JSON.parse(result.data.choices[0].text)
        console.log(parsedRes);
        if (Array.isArray(parsedRes)) {
          setItinerary(parsedRes)
        }
        else {
          const { itinerary } = parsedRes
          if (itinerary) {
            console.log("In else:", parsedRes);
            setItinerary(itinerary)
          }
        }
      } catch (error) {
        console.log({ error, msg: error.message })
      }
      setLoading(false)
    } catch (error) {
      console.log({ error, msg: error.message })
      setLoading(false)
    }
  }

  const getPlacesData_By_GPT_Turbo = async (prompt) => {

    const exampleMessage = [
      {
        role: 'system',
        content: 'Act as a travel places data generator',
      },
      {
        role: 'user',
        content: prompt,
      }
    ]
    try {
      const toastid = showToast('loading', "Please wait we are fetching best travel and tourist places for you!")
      // const completion = await openai.createChatCompletion({
      //   model: 'gpt-3.5-turbo',
      //   messages: exampleMessage,
      // });

      const response = await fetch("https://api.openai.com/v1/chat/completions", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${process.env.REACT_APP_OPENAI_API_KEY}`,
        },
        body: JSON.stringify({
          model: "gpt-3.5-turbo",
          max_tokens: 2048,
          temperature: 0.7,
          messages: exampleMessage,
        }),
      });
      const completion = await response.json()

      const { error } = completion;

      if (error) {
        toast.dismiss(toastid)
        showToast("error", "Internal server error, Please try again", 3000)
        btnRef.current.innerText = "Search Places"
        setBtnDisabled(false)
        return
      }

      let parsedRes = JSON.parse(completion.choices[0].message?.content)

      const { data } = parsedRes;

      const uniquePlacesArray = []

      data.forEach((item, i) => {
        if ((i <= data.length - 1) && data.slice(i + 1).indexOf(item) < 0) {
          uniquePlacesArray.push(item)
        }
      })

      toast.dismiss(toastid)
      // console.log(data, uniquePlacesArray);
      setPlaces(uniquePlacesArray)

      // console.log(parsedRes);
      btnRef.current.innerText = "Generate Itinerary"

    } catch (error) {
      console.log(error);
    }

    setBtnDisabled(false)
  }

  const handleGo = async (e) => {


    // console.log(days);
    // console.log(process.env.REACT_APP_OPENAI_API_KEY_RAJESH);

    const { place, isChecked, state, country } = formData
    const { place: cusPlace } = customPlaceName

    if (places?.length > 0 && ((!isChecked && !place) || (isChecked && !cusPlace))) {
      isChecked ? setCustomPlaceName({ ...customPlaceName, error: true }) : setError(true)
      setBtnDisabled(false)
      return
    }

    setBtnDisabled(true)

    console.log(place);
    if (place || cusPlace) {

      const generatedPrompt = generatePrompt()
      getItineraryData_By_GPT_Turbo(generatedPrompt)
      btnRef.current.innerText = "Generating..."
    }
    else {
      btnRef.current.innerText = "Searching...."

      const prompt = `
            user wants to know all the names of tourist/travel destinations that can be traveled in ${days} days from the country ${country} ${state.name ? `and state ${state.name}` : ""}
            Include all the travel/tourist places including the most famous and least famous ones
            do not repeat the places name in the response array
            Example response json of array of some tourist/travel places in India(maharashtra):
            generated response should be strickly in the form of structured json as shown in the below example

            {
                "data":["Taj-mahal(Agra)","lal-killa(Delhi)","Rajgad(raigad)"]
            }
            `
      getPlacesData_By_GPT_Turbo(prompt)

    }
  }

  const setCommonStates = () => {

    setFormData({
      ...formData,
      place: "",
      isChecked: false
    })

    setItinerary(null)
    setPlaces(null)

    if (btnRef && btnRef.current) {
      btnRef.current.innerText = "Search places"
    }
  }

  const handleCountryChange = (e, value) => {
    setCommonStates()
    if (value) {
      setFormData({
        ...formData,
        country: value.name,
        states: value.states,
        state: { name: "" }
      })
    }
    else {
      setFormData({
        ...formData,
        country: "",
        states: [],
        state: { name: "" }
      })
    }
  }

  if (itinerary) {

    console.log(itinerary);
  }

  useEffect(() => {
    setFormData({ ...formData, endDate: new Date(formData.startDate.getTime() + millisecInOneDay) })
    // eslint-disable-next-line
  }, [formData.startDate])

  const tomorrow = new Date(formData.startDate.getTime() + (millisecInOneDay))
  const dateAfterSixDays = new Date(formData.startDate.getTime() + (millisecInOneDay) * 6)

  return (
    <div>
      <h3 className='text-3xl text-center my-4 itinerary_bg'>✈️ Welcome to Travel Planner ⭐</h3>
      <div className='flex justify-center gap-2 mt-11 mx-2 items-center'>
        <div className="inpts flex gap-3 flex-wrap justify-center max-w-[95%] md:max-w-[90%]">
          <LocalizationProvider dateAdapter={AdapterDayjs} >
            <div className='flex gap-3'>
              <DatePicker disabled={loading} value={dayjs(formData.startDate)} disablePast label="Pick Start date" onChange={(value) => setFormData({ ...formData, startDate: new Date(value) })} name="Startdate" />

              <DatePicker minDate={dayjs(tomorrow)} maxDate={dayjs(dateAfterSixDays)} disabled={loading} disablePast value={dayjs(formData.endDate)} label="Pick End date"
                onChange={(value) => setFormData({ ...formData, endDate: new Date(value) })} />
            </div>
          </LocalizationProvider>
          <div className='flex gap-2 flex-wrap !w-full md:!w-fit justify-center'>
            <Autocomplete
              disabled={loading}
              id="country-select-demo"
              sx={{ width: 300 }}
              options={countriesStates}
              autoHighlight
              getOptionLabel={(option) => option.name}
              renderOption={(props, option) => (
                <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                  <img
                    loading="lazy"
                    width="20"
                    src={`https://flagcdn.com/w20/${option.code2.toLowerCase()}.png`}
                    srcSet={`https://flagcdn.com/w40/${option.code2.toLowerCase()}.png 2x`}
                    alt=""
                  />
                  {option.name} ({option.name})
                </Box>
              )}
              onChange={handleCountryChange}
              renderInput={(params) => (
                <TextField
                  className='w-full'
                  {...params}
                  label="Choose a country"
                  value={formData.country}
                  inputProps={{
                    ...params.inputProps,
                    autoComplete: 'new-password', // disable autocomplete and autofill
                  }}
                />
              )}
              className='!w-full md:!w-[300px]'
            />
            {
              formData.states.length > 0
              &&
              <Autocomplete
                disabled={loading}
                className='!w-full md:!w-[300px]'
                id="country-select-demo"
                sx={{ width: 300 }}
                options={formData.states}
                autoHighlight
                getOptionLabel={(option) => option.name}
                onChange={(e, value) => { setCommonStates(); setFormData({ ...formData, state: value ? value : { name: "" } }) }}
                value={formData.state}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label={<p>Choose State <small>(optional)</small></p>}
                    inputProps={{
                      ...params.inputProps,
                      autoComplete: 'new-password', // disable autocomplete and autofill
                    }}
                  />
                )}
              />
            }
          </div>
          <div className='flex gap-2 flex-wrap !w-full md:!w-fit justify-center'>
            {
              places
              &&
              <>
                <div className='relative w-full md:w-fit'>
                  <Autocomplete
                    disabled={loading || formData.isChecked}
                    className='!w-full md:!w-[300px]'
                    id="country-select-demo"
                    sx={{ width: 300 }}
                    options={places}
                    autoHighlight
                    getOptionLabel={(option) => option}
                    onChange={(e, value) => { setFormData({ ...formData, place: value }); setError(false); setItinerary(null) }}
                    value={formData.place}
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        label="Choose place to travel"
                        error={error}
                        autoComplete='off'
                        inputProps={{
                          ...params.inputProps,
                          autoComplete: 'new-password', // disable autocomplete and autofill
                        }}
                      />
                    )}
                  />
                  {
                    error && <FormHelperText error={error}>Please select the place</FormHelperText>
                  }
                  <div className='md:absolute'>
                    <FormControlLabel disabled={loading} className='text-gray-500' control={<Checkbox size='small'
                      onChange={() => {
                        setFormData({ ...formData, isChecked: !formData.isChecked, place: "" });
                        setError(false)
                        setCustomPlaceName({ ...customPlaceName, error: false })
                      }} />} label="Can't find the place?" />
                  </div>
                </div>
                {
                  formData.isChecked
                  &&
                  <div className='flex flex-col w-full md:w-fit'>
                    <TextField error={customPlaceName.error} disabled={loading} className='!w-full md:!w-[300px]' id="outlined-basic" label="Mention your place" variant="outlined" value={customPlaceName.place}
                      onChange={(e) => {
                        setCustomPlaceName({ error: false, place: e.target.value })
                        setItinerary(null)
                      }} />
                    {
                      customPlaceName.error && <FormHelperText error={customPlaceName.error}>Please mention your place</FormHelperText>
                    }
                  </div>

                }
              </>
            }
            {
              formData.country
              &&
              <button ref={btnRef} disabled={btnDisabled} onClick={handleGo} className='disabled:bg-indigo-300 disabled:hover:bg-indigo-300 px-4 !py-3 md:!py-2 w-full md:w-fit bg-indigo-500 hover:bg-indigo-600 text-white text-xl rounded-md md:h-14'>
                Search places
              </button>
            }

          </div>
        </div>
      </div>
      <Container maxWidth="md" className='mt-10 shadow-md rounded-md !px-2 md:!px-5 py-[3rem]' >
        {
          loading ?
            <>
              <div className='w-full flex justify-center flex-col gap-2 items-center'>
                <img className='w-20' src="https://media.tenor.com/siXiZO-F1awAAAAC/loader-waiting.gif" alt="" />
                <p className='roboto font-medium text-sm'>Please wait we are generating your itinerary!</p>
              </div>
            </>
            :
            itinerary
            &&
            <div className="itinerary">
              <h1 className='roboto !text-xl md:!text-2xl text-indigo-500 text-center'>Your {days} days itinerary plan to <span className='inline-block'>( {formData.place || customPlaceName.place} )</span> is ready,<span className='inline-block'> &nbsp; Happy and Safe Journey!</span></h1>
              {
                itinerary.data.map((it, i) => {
                  const { activities, departure_activity } = it;
                  return (
                    <div className={`mt-8 itinerary-box left-bar`} key={it.day}>
                      <h1 className='text-2xl font-semibold'>
                        <i className="fa-solid fa-circle-dot text-gray-500 !text-lg"></i>
                        <span className='ml-3'>{it.day}</span>
                      </h1>
                      <div className='ml-12 mt-2 flex flex-col gap-2'>
                        {
                          (activities ? activities : departure_activity).map((act, i) => {
                            return (
                              <ActivityDetail key={i} i={i} act={act} it={it} />
                            )
                          })
                        }
                        {it.dine_options?.length > 0 && <div>
                          <DiningDetails it={it} />
                        </div>}
                      </div>
                    </div>
                  )
                })
              }
            </div>

        }
      </Container>
    </div >
  )
}

export default Itinerary
