import React from 'react'
import Dining from './Dining'

const DiningDetails = ({ it }) => {
  return (
    <>
      <h3 className='text-2xl flex gap-2 font-bold text-indigo-400 mt-3'>
        Dining Options
        <img className='w-8' src="	https://cdn-icons-png.flaticon.com/512/2251/2251775.png" alt="" />
      </h3>
      <div className='mt-3 flex w-full gap-4 flex-wrap'>
        {
          it.dine_options.map((dine, i) => {
            return (
              <Dining key={i} dine={dine} i={i} />
            )
          })
        }
      </div>

    </>
  )
}

export default DiningDetails
