import React from 'react'

const Dining = ({ i, dine }) => {

  let { place, address } = dine
  return (
    <div key={i} className='!w-[97%] md:!w-[47%] border-[1px] border-indigo-400 p-2 rounded-md bg-indigo-50 flex flex-col gap-2'>
      <div>
        <i className="text-gray-500 text-sm fa-solid fa-hotel mr-3"></i>
        <span className='text-sm font-medium text-black'>{place}</span>
      </div>
      <div>
        <i className="text-gray-500 text-sm fa-solid fa-location-dot mr-3"></i>
        <span className='text-sm font-medium text-black'>{address}</span>
      </div>
    </div>
  )
}

export default Dining
