import React from 'react'
import './ActivityDetail.css'

const ActivityDetail = ({ i, it, act }) => {
    let time = Object.keys(act)[0]

    return (
        <div className={`activity_detail ${i + 1 !== it.activities.length && 'left-bar'}`}>
            <h1 className='text-2xl font-bold text-gray-400'>{time}</h1>
            <div className='!ml-6 md:!ml-8 mt-2 flex flex-col gap-2'>
                <h1 className='text font-semibold'>{act[time].activity}</h1>
                <p className='text-sm font-medium text-gray-600'>{act[time].description}</p>
                <p>
                    <span className='text-sm font-bold mr-2'>Transportation:</span>
                    <span>{act[time].transportation}</span>
                </p>
                {act[time].coordinates && Object.keys(act[time].coordinates).length > 0 && 
                <p className='flex flex-col md:!flex-row gap-1 md:items-center'>
                    <span className='text-sm font-bold mr-2'>Co-ordinates:</span>
                    <span>
                        <span>
                            <span className='text-gray-800 font-medium'>latitude: </span>
                            <span className='roboto !text-xs px-2 py-[.15rem] !bg-green-100 rounded-full border-[1px] !border-green-400'>{act[time].coordinates.latitude}</span>
                        </span>,&nbsp;
                        <span>
                            <span className='text-gray-800 font-medium'>longitude: </span>
                            <span className='roboto !text-xs px-2 py-[.15rem] bg-green-100 rounded-full border-[1px] border-green-400'>{act[time].coordinates.longitude}</span>
                        </span>
                    </span>
                </p>}
            </div>
        </div>
    )
}

export default ActivityDetail
