import React from 'react'
import Itinerary from './pages/Itinerary/Itinerary.js'
import './App.css'
import { Toaster } from 'react-hot-toast'

const App = () => {

  return (
    <>

      <Itinerary />
      <Toaster
        position="top-center"
        reverseOrder={true}
      />

    </>

  )
}

export default App
